.home-page {
    text-align: center;
    width: 100%;
    height: 100vh;
}
.home-page span.name {
    width: 100%;
    display: inline-block;
    margin: 20px 0px;
    font-size: 70px;
    font-weight: bold;
    color: #bbb;
    letter-spacing: 50px;
}
.home-page .your-image {
    width: 40%;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-right: 25px;
}
.home-page .your-image .header-input input#group_image {
    width: 100%;
    border: 1px solid #ccc;
    padding: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 5px;
}
.home-page .your-image .content-input {
    margin-top: 20px;
}
.home-page .your-image .content-input .image-input {
    width: 100%;
    min-height: 300px;
    max-height: 300px;
    overflow: scroll;
    background: #f0f0f0;
    border-radius: 5px;
    border: 1px solid #e0e0e0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.home-page .your-image .content-input .message-input #message {
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-top: 20px;
    border-radius: 5px;
    border: 1px solid #ccc;
    min-width: 100%;
    min-height: 100px;
    max-width: 100%;
    padding: 10px;
    font-size: 15px;
}
.home-page .your-image .content-input .message-input #message:focus{
    outline: none;
}
.home-page .your-image .content-input  span {
    font-size: 17;
    font-weight: 100;
    background: lightseagreen;
    color: #fff;
    padding: 10px 30px;
    border-radius: 5px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: inline-block;
    float: right;
    margin-top: 15px;
    cursor: pointer;
    -webkit-transition: all 0.1s ease-in;
    -o-transition: all 0.1s ease-in;
    transition: all 0.1s ease-in;
}
.home-page .your-image .content-input  span:hover{
    background: #177771;
}
.home-page .right {
    width: 40%;
    display: inline-block;
    margin-left: 25px;
    vertical-align: top;
}
.home-page .right .out-put {
    width: 100%;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 10px;
    vertical-align: top;
    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.home-page .out-put canvas#new-image {
    width: 100%;
    margin-top: 20px;
    border-radius: 5px;
    background: #f0f0f0;
    border: 1px solid #e0e0e0;
    min-height: 300px;
    max-height: 300px;
    overflow: scroll;
}
.home-page .out-put .out-put-title {
    text-align: left;
    font-weight: 100;
    line-height: 40px;
}
.home-page .out-put a#downloader {
    margin-top: 20px;
    display: block;
    text-decoration: none;
    font-weight: 100;
    font-size: 20px;
    border: 1px solid;
    padding: 10px;
    -webkit-transition: all 0.1s ease-in;
    -o-transition: all 0.1s ease-in;
    transition: all 0.1s ease-in;
    border-radius: 5px;
}
.home-page .out-put a#downloader:hover {
    background: #4D2485;
    color: #fff;
}
.info {
    margin-top: 30px;
}
.info span {
    display: inline-block;
    width: 100%;
    text-align: right;
    padding: 5px 20px;
    font-size: 17px;
    font-weight: 100;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.home-page .your-image .header-input input#group_image:focus{
    outline: none;
    -webkit-box-shadow: 0px 0px 5px #ccc;
            box-shadow: 0px 0px 5px #ccc;
}


